/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import PeopleAltOutlinedIcon from '@mui/icons-material/PeopleAltOutlined';
import { Col, Container, Form, Button, Spinner, Row } from 'react-bootstrap';
import { useFormik } from 'formik';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import Cookies from 'js-cookie';
import { useHistory } from 'react-router';
import { routers } from 'router/router';
import CheckboxInput from 'components/input/CheckboxInput';
import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import YesNoSwitch from 'components/input/YesNoSwitch';
import { getHousehold, updateHousehold } from 'app/family/familySlice';
import { BoolToString, formatBoolean } from 'components/helper/helper';
import LoadingContent from 'components/LoadingContent';

interface Props {}

const Household = (props: Props) => {
  const [loading, setLoading] = React.useState(false);
  const formHousehold = useFormik({
    initialValues: {
      is_parent_guardian: false,
      is_sibbling: false,
      is_spouse: false,
      is_child: false,
      is_just_me: false,
    },
    onSubmit: () => {},
  });
  const dispatch = useAppDispatch();
  const family = useAppSelector((store) => store.family);
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      who_in_household_array: '',
      household_size: 0,
      whom_live_permanently: '',
      parent_martial_status: '',
      household_income: '',
      is_have_any_children: true,
    },
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        who_in_household_array: JSON.stringify(formHousehold.values),
        is_have_any_children: formatBoolean(values.is_have_any_children),
        student_id: parseInt(studentId || ''),
      };
      const responese = await dispatch(updateHousehold(newValues));
      if (responese.payload) history.push(routers.parentNo1.path);
    },
  });

  const studentId = Cookies.get('userId');
  const getDetail = async () => {
    setLoading(true);
    let res = await dispatch(getHousehold(studentId));
    const dataLoaded: any = res.payload;
    const values: any = {
      ...dataLoaded,
      is_have_any_children: BoolToString(dataLoaded.is_have_any_children),
    };
    formHousehold.setValues(JSON.parse(dataLoaded.who_in_household_array));
    formik.setValues(values);
    setLoading(false);
  };
  React.useEffect(() => {
    getDetail();
  }, []);

  const householdOptions = [
    { value: 'Parents(s)/Guardian(s)', key: 'is_parent_guardian' },
    { value: 'Sibbling(s)', key: 'is_sibbling' },
    { value: 'Spouse', key: 'is_spouse' },
    { value: 'Child(ren)', key: 'is_child' },
    { value: 'Just me', key: 'is_just_me' },
  ];
  return loading ? (
    <LoadingContent />
  ) : (
    <div>
      <TopProcessBar
        stepProcess={[
          { title: 'General', percentOnProcess: 30 },
          { title: 'Parental Marital Status', percentOnProcess: 45 },
          { title: 'Household Income', percentOnProcess: 70 },
          { title: 'Dependents', percentOnProcess: 88 },
        ]}
        title="Family Information"
        process={1}
        icon={<PeopleAltOutlinedIcon />}
      />

      <Container className="my-4">
        <div className="d-flex justify-content-center">
          <Form style={{ maxWidth: '1000px', width: '100%' }} onSubmit={formik.handleSubmit}>
            <Row className="m-0">
              <div className="text-dark fs-3 fw-bold">Household</div>
              <div className="text-muted">Who is in your household? (select all that apply)</div>
              {householdOptions.map((option, index) => {
                const key = option.key;
                const formValues: any = formHousehold.values;
                return (
                  <Col md={4} lg={3} key={index}>
                    <CheckboxInput
                      label={<span className="mt-2 text-muted">{option.value}</span>}
                      name={key}
                      values={formValues[key]}
                      onChange={formHousehold.handleChange}
                    />
                  </Col>
                );
              })}
              <div />
              <SelectInput
                option={[
                  { value: 1 },
                  { value: 2 },
                  { value: 3 },
                  { value: 4 },
                  { value: 5 },
                  { value: 6 },
                  { value: 7 },
                  { value: 8 },
                  { value: 9 },
                  { value: 10 },
                  { value: 11 },
                  { value: 12 },
                  { value: 13 },
                  { value: 14 },
                  { value: 15 },
                  { value: 16 },
                  { value: 17 },
                  { value: 18 },
                  { value: 19 },
                  { value: 20 },
                ]}
                label="Household Size *"
                name="household_size"
                values={formik.values.household_size}
                onChange={formik.handleChange}
              />
              <div className="w-100 border my-3" />
              <div>Parental Marital Status</div>
              <SelectInput
                option={[
                  { value: 'Both Parents/Guardians' },
                  { value: 'Parent/Guardian 1' },
                  { value: 'Parent/Guardian 2' },
                  { value: 'Ward of State/Court' },
                  { value: 'Other' },
                ]}
                label="With whom do you live permanently? *"
                name="whom_live_permanently"
                values={formik.values.whom_live_permanently}
                onChange={formik.handleChange}
              />
              <div />
              <SelectInput
                option={[
                  { value: 'Married' },
                  { value: 'Separated' },
                  { value: 'Divorced' },
                  { value: 'Never Married' },
                  { value: 'Widowed' },
                  { value: 'Civil Union/Domestic Partners' },
                ]}
                label="Parents'/Guardians' Marital Status (relative to each other) *"
                name="parent_martial_status"
                values={formik.values.parent_martial_status}
                onChange={formik.handleChange}
              />
              <div className="w-100 border my-3" />
              <div>Household Income</div>
              <TextInput
                type="number"
                label="What was the total income earned in 2019 by your parents or legal guardians?"
                name="household_income"
                values={formik.values.household_income}
                onChange={formik.handleChange}
              />
              <div />
              <div className="w-100 border my-3" />
              <div>Dependents</div>
              <YesNoSwitch
                name="is_have_any_children"
                values={formik.values.is_have_any_children}
                onChange={formik.handleChange}
              />
            </Row>
            <div className="my-3 d-flex justify-content-end border-top pt-3">
              <div className="col col-md-3">
                <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                  {family.status === 'loading' ? (
                    <Spinner size="sm" animation="border" variant="primary" />
                  ) : (
                    'Continue'
                  )}
                </Button>
              </div>
            </div>
          </Form>
        </div>
      </Container>
    </div>
  );
};

export default Household;
