import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Col, Row, Button, Image } from 'react-bootstrap';
import SelectInput from 'components/input/SelectInput';
import SearchIcon from '@mui/icons-material/Search';
import RemoveIcon from '@mui/icons-material/Remove';
import AddIcon from '@mui/icons-material/Add';
import TextIconInput from 'components/input/TextIconInput';
import TextInput from 'components/input/textInput';
import { useFormik } from 'formik';
interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}

const ExpandMore = styled((props: ExpandMoreProps) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));

interface Props {
  handleSave: (value: any) => void;
  indexText: string;
  item: any;
}

const EducationConfig = (props: Props) => {
  const formItem = useFormik({
    initialValues: {
      degreesNumber: '',
      degreesReceived: '',
      graduationYear: '',
    },
    onSubmit: (values) => {},
  });
  const [expanded, setExpanded] = React.useState(false);
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  React.useEffect(() => {
    if (props.item && props.item.school) {
      setSelectedEducation(props.item.school);
      formItem.setValues(props.item);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const schoolOption = {
    iconSrc: '/assets/icons/school-8.png',
    name: 'University of California',
    isPublic: true,
    location: 'Lansing, Michigan',
  };
  const [selectedEducation, setSelectedEducation] = React.useState<any>(undefined);
  return (
    <Row className="m-0">
      <Card className="w-100 my-3 px-0">
        <CardActions
          disableSpacing
          onClick={handleExpandClick}
          sx={{
            backgroundColor: expanded ? '#F5F1FF' : 'none',
            borderBottom: expanded ? '2px solid' : 'none',
            borderBottomColor: expanded ? '#5F30E2' : 'none',
          }}
        >
          <div className="w-100 d-flex justify-content-between">
            <span className="p-1">{selectedEducation?.name}</span>
            <strong className="text-muted">{props.indexText}</strong>
          </div>
          <ExpandMore
            expand={expanded}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </ExpandMore>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Row>
              <div className="text-muted">
                Search for your parent graduation College / University
              </div>
              <TextIconInput icon={<SearchIcon />} mdCol="12" />
              <Card className="border rounded m-1 py-3">
                <div className="d-flex align-items-end position-relative">
                  <div className="text-center mx-3 me-3" style={{ width: 80 }}>
                    <Image height={34} src={schoolOption.iconSrc} />
                  </div>
                  <div className="me-3 pb-2">{schoolOption.name}</div>
                  <div className="text-muted sized70  pb-2">{schoolOption.location}</div>
                  <span className="position-absolute h-100 d-flex align-items-center end-0">
                    <AddIcon
                      fontSize="small"
                      className="bg-app text-primary rounded-circle mx-4 pointer"
                      onClick={() => setSelectedEducation(schoolOption)}
                    />
                  </span>
                </div>
              </Card>
              <div className="my-2">Parent Graduation College</div>
              {selectedEducation && (
                <div className="d-flex align-items-end position-relative">
                  <div className="text-center mx-3 me-3" style={{ width: 80 }}>
                    <Image height={34} src={selectedEducation.iconSrc} />
                  </div>
                  <div className="me-3 pb-2">{selectedEducation.name}</div>
                  <div className="text-muted sized70  pb-2">{selectedEducation.location}</div>
                  <span className="position-absolute h-100 d-flex align-items-center end-0">
                    <RemoveIcon
                      fontSize="small"
                      className="bg-light text-dart rounded-circle mx-4 pointer"
                      onClick={() => {
                        setSelectedEducation(undefined);
                      }}
                    />
                  </span>
                </div>
              )}
              <SelectInput
                option={[
                  { value: 'No degree' },
                  { value: '1' },
                  { value: '2' },
                  { value: '3' },
                  { value: '4' },
                  { value: '5' },
                ]}
                label={'Degrees Number'}
                name="degreesNumber"
                values={formItem.values.degreesNumber}
                onChange={formItem.handleChange}
              />
              <SelectInput
                option={[
                  { value: `Associate's (AA, AS)` },
                  { value: `Bachelor's (BA, BS)` },
                  { value: `Master's (MA, MS)` },
                  { value: `Business (MBA, MAcc)` },
                  { value: `Law (JD, LLM)` },
                  { value: `Medicine (MD, DO, DVM, DDS)` },
                  { value: `Doctorate (PhD, EdD, etc)` },
                  { value: 'Other' },
                ]}
                label={'Degree Received'}
                name="degreesReceived"
                values={formItem.values.degreesReceived}
                onChange={formItem.handleChange}
              />
              <TextInput
                name="graduationYear"
                values={formItem.values.graduationYear}
                onChange={formItem.handleChange}
                type="number"
                label="Graduation Year"
              />
              <Col xs={6} md={9} />

              <Col xs={6} md={3} className="p-3">
                <Button
                  className="w-100"
                  onClick={() => {
                    props.handleSave({
                      school: selectedEducation,
                      ...formItem.values,
                    });
                    setExpanded(false);
                  }}
                >
                  Save Info
                </Button>
              </Col>
            </Row>
          </CardContent>
        </Collapse>
      </Card>
    </Row>
  );
};

export default EducationConfig;
