import LoadingContent from 'components/LoadingContent';
import { useFormik } from 'formik';
import React from 'react';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import PeopleAltOutlinedIcon from '@mui/icons-material/PeopleAltOutlined';
import { Container, Form, Row, Spinner, Button } from 'react-bootstrap';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import SelectInput from 'components/input/SelectInput';
import SiblingsConfig from './SiblingsConfig';
import { getSiblings, updateSiblings } from 'app/family/familySlice';
import Cookies from 'js-cookie';
import { useHistory } from 'react-router';
import { routers } from 'router/router';

interface Props {}

const Siblings = (props: Props) => {
  const [loading, setLoading] = React.useState(false);
  const dispatch = useAppDispatch();
  const family = useAppSelector((store) => store.family);
  const [arraySiblings, setArraySiblings] = React.useState<any[]>([]);
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      number_of_siblings: '',
      siblings_array: '',
    },
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        student_id: parseInt(studentId || ''),
        siblings_array: JSON.stringify(arraySiblings),
      };
      const responese = await dispatch(updateSiblings(newValues));
      if (responese.payload) history.push(routers.parentNo1.path);
    },
  });
  const studentId = Cookies.get('userId');
  const getDetail = async () => {
    setLoading(true);
    //
    let res = await dispatch(getSiblings(studentId));
    const dataLoaded: any = res.payload;
    const values: any = {
      ...dataLoaded,
    };
    formik.setValues(values);
    if (dataLoaded.siblings_array) {
      const arraySibling = await JSON.parse(dataLoaded.siblings_array);
      setArraySiblings(arraySibling);
    }

    setLoading(false);
  };
  React.useEffect(() => {
    getDetail();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  React.useEffect(() => {
    const length = parseInt(formik.values.number_of_siblings) || 0;
    const newArraySiblings = [...arraySiblings, ...Array(length)].slice(0, length);
    setArraySiblings(newArraySiblings);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formik.values.number_of_siblings]);
  return loading ? (
    <LoadingContent />
  ) : (
    <>
      <TopProcessBar
        stepProcess={[]}
        title="Family Information"
        process={1}
        icon={<PeopleAltOutlinedIcon />}
      />

      <Container className="my-4">
        <div className="d-flex justify-content-center">
          <Form style={{ maxWidth: '1000px', width: '100%' }} onSubmit={formik.handleSubmit}>
            <Row className="m-0">
              <div className="text-dark fs-3 fw-bold">Siblings</div>
              <SelectInput
                name="number_of_siblings"
                values={formik.values.number_of_siblings}
                onChange={formik.handleChange}
                label="Please specify number of siblings you have*"
                option={[
                  { value: 0 },
                  { value: 1 },
                  { value: 2 },
                  { value: 3 },
                  { value: 4 },
                  { value: 5 },
                ]}
              />
              <div />
              {arraySiblings.map((item, index) => (
                <SiblingsConfig
                  key={index}
                  item={item}
                  handleSave={(values) => {
                    const newArraySiblings = [...arraySiblings];
                    newArraySiblings.splice(index, 1, values);
                    setArraySiblings(newArraySiblings);
                  }}
                />
              ))}
            </Row>
            <div className="m-3 d-flex justify-content-end border-top pt-3">
              <div className="col col-md-3">
                <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                  {family.status === 'loading' ? (
                    <Spinner size="sm" animation="border" variant="primary" />
                  ) : (
                    'Continue'
                  )}
                </Button>
              </div>
            </div>
          </Form>
        </div>
      </Container>
    </>
  );
};

export default Siblings;
