import React from 'react';
import SelectInput from 'components/input/SelectInput';
import GroupRadioButton from 'components/input/GroupRadioButton';
import EducationConfig from './EducationConfig';
import { educationLevelOptions } from 'components/helper/helper';

interface Props {
  formik: any;
  educationData: any;
}

const Education = (props: Props) => {
  const [arrayEducation, setArrayEducation] = React.useState<any[]>([]);
  React.useEffect(() => {
    const length = parseInt(props.formik.values.total_number_institutions_attended);
    if (length) {
      const newArrayEducation = [...arrayEducation, ...Array(length)].slice(0, length);
      setArrayEducation(newArrayEducation);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.formik.values.total_number_institutions_attended]);

  React.useEffect(() => {
    props.formik.setFieldValue('parent_graduation_college_array', JSON.stringify(arrayEducation));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [arrayEducation]);
  React.useEffect(() => {
    setArrayEducation(props.educationData);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <div>Education</div>
      <SelectInput
        option={educationLevelOptions}
        name="education_level"
        values={props.formik.values.education_level}
        onChange={props.formik.handleChange}
        label="Education Level"
      />
      <SelectInput
        option={educationLevelOptions}
        label="Highest Level of education?"
        name="highest_level_education"
        values={props.formik.values.highest_level_education}
        onChange={props.formik.handleChange}
      />
      <div className="text-muted">Total number of institutions attended</div>
      <GroupRadioButton
        mdCol={1}
        name="total_number_institutions_attended"
        values={props.formik.values.total_number_institutions_attended}
        onChange={props.formik.handleChange}
        options={[{ value: 1 }, { value: 2 }, { value: 3 }, { value: 4 }, { value: 5 }]}
      />
      {arrayEducation.map((item, index) => (
        <EducationConfig
          key={index}
          item={item}
          indexText={`${index + 1}/${arrayEducation.length}`}
          handleSave={(values) => {
            const newArrayEducation = [...arrayEducation];
            newArrayEducation.splice(index, 1, values);
            setArrayEducation(newArrayEducation);
          }}
        />
      ))}
    </>
  );
};

export default Education;
