import LoadingContent from 'components/LoadingContent';
import { useFormik } from 'formik';
import React from 'react';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import PeopleAltOutlinedIcon from '@mui/icons-material/PeopleAltOutlined';
import { Col, Container, Form, Row, Spinner, Button } from 'react-bootstrap';
import YesNoSwitch from 'components/input/YesNoSwitch';
import CheckboxInput from 'components/input/CheckboxInput';
import GroupRadioButton from 'components/input/GroupRadioButton';
import SelectInput from 'components/input/SelectInput';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import BasicContact from './components/BasicContact';
import Employment from './components/Employment';
import Education from './components/Education';
import Cookies from 'js-cookie';
import { useHistory } from 'react-router';
import { routers } from 'router/router';
import { getParent2, updateParent2 } from 'app/family/familySlice';
import { BoolToString, formatBoolean } from 'components/helper/helper';

interface Props {}

const Parent2 = (props: Props) => {
  const [loading, setLoading] = React.useState(false);
  const [educationData, setEducationData] = React.useState([]);
  const dispatch = useAppDispatch();
  const history = useHistory();
  const family = useAppSelector((store) => store.family);
  const formik = useFormik({
    initialValues: {
      is_parent_2_living: 'true',
      is_parent_2_decesead: true,
      relationship_type: '',
      relationship_to_you: '',
      prefix: '',
      first_name: '',
      middle_name: '',
      last_name: '',
      former_last_name: '',
      suffix: '',
      birth_country: '',
      prefered_email: '',
      country_code: '',
      phone_number: '',
      phone_number_type: '',
      parent_address_is: '',
      parent_current_job_status: '',
      job_category: '',
      employment_status: '',
      place_of_work: '',
      ocupation_and_position_title: '',
      occupation: '',
      parent_currently_employed_or_retire_from_college: '',
      education_level: '',
      highest_level_education: '',
      total_number_institutions_attended: 0,
      parent_graduation_college_array: '',
    },
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        is_parent_2_living: formatBoolean(values.is_parent_2_living),
        student_id: parseInt(studentId || ''),
      };
      const responese = await dispatch(updateParent2(newValues));
      if (responese.payload) history.push(routers.siblings.path);
    },
  });
  const studentId = Cookies.get('userId');

  const getDetail = async () => {
    setLoading(true);
    //
    let res = await dispatch(getParent2(studentId));
    const dataLoaded: any = res.payload;
    const values: any = {
      ...dataLoaded,
      is_parent_2_living: BoolToString(dataLoaded.is_parent_2_living),
    };
    formik.setValues(values);
    if (dataLoaded.parent_graduation_college_array) {
      const newEducationData = JSON.parse(dataLoaded.parent_graduation_college_array);
      setEducationData(newEducationData);
    }
    setLoading(false);
  };
  React.useEffect(() => {
    getDetail();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return loading ? (
    <LoadingContent />
  ) : (
    <>
      <TopProcessBar
        stepProcess={[
          { title: 'General', percentOnProcess: 25 },
          { title: 'Relationship', percentOnProcess: 40 },
          { title: 'Basic', percentOnProcess: 55 },
          { title: 'Employment', percentOnProcess: 70 },
          { title: 'Education', percentOnProcess: 88 },
        ]}
        title="Family Information"
        process={1}
        icon={<PeopleAltOutlinedIcon />}
      />

      <Container className="my-4">
        <div className="d-flex justify-content-center">
          <Form style={{ maxWidth: '1000px', width: '100%' }} onSubmit={formik.handleSubmit}>
            <Row className="m-0">
              <div className="text-dark fs-3 fw-bold">Parent No.2</div>
              <Col md={6}>
                <div className="text-muted">Is “Parent No.2” living?</div>
                <YesNoSwitch
                  name="is_parent_2_living"
                  values={formik.values.is_parent_2_living}
                  onChange={formik.handleChange}
                />
              </Col>
              <Col md={6}>
                <div className="text-muted">Please check if deceased</div>
                <CheckboxInput
                  label={<span className="text-muted">“Parent No.2” is decesead</span>}
                  name="is_parent_2_decesead"
                  values={formik.values.is_parent_2_decesead}
                  onChange={formik.handleChange}
                />
              </Col>
              <div className="border w-100 my-3" />
              <div>Relationship</div>
              <div className="text-muted">Type</div>
              <GroupRadioButton
                values={formik.values.relationship_type}
                name="relationship_type"
                onChange={formik.handleChange}
                options={[
                  { value: 'Mother' },
                  { value: 'Father' },
                  { value: 'I have limited information about this parent', mdCol: 6 },
                ]}
              />
              <div></div>
              <SelectInput
                option={[
                  { value: 'Father' },
                  { value: 'Mother' },
                  { value: 'Brother' },
                  { value: 'Sister' },
                  { value: 'Grandfather' },
                  { value: 'Grandmother' },
                  { value: 'Step Father' },
                  { value: 'Step Mother' },
                  { value: 'Legal Guardian' },
                  { value: 'Other' },
                ]}
                label="Relationship to you"
                name="relationship_to_you"
                values={formik.values.relationship_to_you}
                onChange={formik.handleChange}
              />
              <div className="border w-100 my-3" />
              <BasicContact formik={formik} />
              <div className="border w-100 my-3" />
              <Employment formik={formik} parentNumber={2} />
              <div className="border w-100 my-3" />
              <Education formik={formik} educationData={educationData} />
            </Row>
            <div className="my-3 d-flex justify-content-end border-top pt-3">
              <div className="col col-md-3">
                <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                  {family.status === 'loading' ? (
                    <Spinner size="sm" animation="border" variant="primary" />
                  ) : (
                    'Continue'
                  )}
                </Button>
              </div>
            </div>
          </Form>
        </div>
      </Container>
    </>
  );
};

export default Parent2;
